import { FC } from 'react';

import TextBlock from '../TextBlock';
import { CollapsibleProps } from '../TextBlock.types';

import { Wrapper, CustomDropdown } from './Collapsible.styles';

const Collapsible: FC<CollapsibleProps> = ({
  id,
  lines = 6,
  prompt = 'Continue reading...',
  className,
  children,
}) => (
  <Wrapper $lines={lines} className={className}>
    <CustomDropdown id={id} trigger={prompt}>
      <TextBlock>{children}</TextBlock>
    </CustomDropdown>
  </Wrapper>
);

export default Collapsible;
